<template>
 <h3 style="text-align: left;">{{ $t('Thông tin chi tiết các loại vật phẩm:') }}</h3>
            <el-divider />
            <table v-if="this.medicineLs != null && this.medicineLs.length > 0 ">
                <tr style="font-weight: bold;">
                    <td>{{ $t('Số thứ tự') }}</td>
                    <td>{{ $t('Số hiệu') }}</td>
                    <td>{{ $t('Tên hàng') }}</td>
                    <td>{{ $t('Quy cách') }}</td>
                   
                </tr>
                <tr v-for="(medicine, index) in this.medicineLs" :key="medicine">
                    <td>{{ index + 1 }}</td>
                    <td>{{ medicine.F_MEDICINE_ID }}</td>
                    <td>{{ medicine.F_MEDICINE_NAME_VI + "/" + medicine.F_MEDICINE_NAME_EN }}</td>
                    <td>{{ medicine.F_MEDICINE_OBJ_UNIT_VI + "/" + medicine.F_MEDICINE_OBJ_UNIT_EN }}</td>
                </tr>
                </table>
</template>
<script>
import api from "@/model/api.js"
import { ElMessage } from 'element-plus'
export default
{
    name:"GoodsInfor",
    data(){
        return {
            medicineLs:[]
        }
    },
    methods:{
        GetAllMedicineInfor()
        {
            this.medicineLs=[];
            api.GetAllMedicineInfor(
                (dat)=>{
                    if(dat?.data)
                    {
                        if (dat?.data?.status == "success")
                        {
                            this.medicineLs= dat?.data?.data;
                        }
                        else   //error
                        {
                            ElMessage.error(dat?.data?.message)
                        }
                    }
                },
                (er)=>{
                    ElMessage.error(er.message)
                })
        }
    },
     mounted(){
        this.GetAllMedicineInfor();
     }
}
</script>
<style scoped>
table,
th,
td {
    padding: 10px;
    border: 1px solid black;
    border-collapse: collapse;
}
</style>